/*}*/

#events,#news{
    font-size: 1.5em;
    color: #C92627;
}

.events-link i span,.news-link i span{
    font-family: Gilroy-ExtraBold,sans-serif;
}

.events-news a:hover{
    background-color:#C92627;
}
.events-link:hover > #events,.news-link:hover > #news{
    color: whitesmoke;
}

.events-news .gt-logo{
    position: absolute;
    top:5%;
    left:2%;
}

.events-news .gt-logo img{
    width: 100px;
    height: 100px;
}

@media (max-width: 720px) {
    .events-news a {
        margin: 35% 5% 0 5%;
        background-color: whitesmoke;
        padding: 4% 6%;
        border-radius: 20px;
        opacity: 1;
    }
    .events-news .gt-logo{
        position: absolute;
        top:8%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .events-news .gt-logo img{
        width: 100px;
        height: 100px;
    }
}